.head-text {
    display: block;
    width: 100%;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.full-50 {
    width: 56% !important;
}

/*new css*/
.accpart mat-label,
.accpart label {
    width: 170px;
    font-weight: bold;
}

.accpart {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

::ng-deep .view-user .mat-form-field-appearance-outline .mat-form-field-outline-end,
::ng-deep .view-user .mat-form-field-appearance-outline .mat-form-field-outline-start {
    background: transparent !important;
}

.htext {
    padding-bottom: 15px;
}

.mat-warn {
    padding: 0 40px;
    background: #f65c00 !important;
}

.cncl-btn {
    background: #f4f4f4 !important;
}

@media screen and (max-width: 767px) {
    .page-wrapper.editpart {
        padding: 20px 0 0 0 !important;
    }

    .title-container {
        margin: 0px 0 20px;
        background: #fff;
        padding: 15px;
        border-top: 2px solid #d9d3d3;
        border-bottom: 2px solid #d9d3d3;
    }

    .portal-outer.view-user.result_block {
        padding: 0 28px;
    }

    .portal-outer {
        height: inherit !important;
        margin-bottom: 25px;
    }

    .accpart mat-label,
    .accpart label {
        width: 100%;
    }

    .full-50 {
        width: 100% !important;
    }

    .accpart {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        flex-wrap: wrap;
    }

    .mat-card {
        background: #f0eff4;
    }

    ::ng-deep .view-user .mat-form-field-wrapper {
        background: #ffffff;
    }

    ::ng-deep #content {
        background: #ffffff;
    }

    .headtext {
        margin-bottom: 0;
        width: 100%;
        display: block !important;
    }

    .head-text {
        display: none !important;
    }

    .htext {
        padding-top: 15px;
    }
}

.head-text {
    display: block;
}

.headtext {
    display: none;
}

.view-edit {
    margin-right: 37px;
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {

    .accpart mat-label,
    .accpart label {
        width: 103px;
    }
}

@media only screen and (min-device-width: 810px) and (max-device-width: 1080px) {

    .accpart mat-label,
    .accpart label {
        width: 103px;
    }
}

@media only screen and (min-device-width: 810px) and (max-device-width: 1080px) and (orientation: landscape) {

    .accpart mat-label,
    .accpart label {
        width: 103px;
    }

    .accpart label:first-child {
        padding-top: 4px;
    }
}